@use "../../styles/variables" as *;
@use "../../styles/functions" as *;

.debit-card {
  background-image: url("../../../public/images/card.png"),
    linear-gradient(89.12deg, #202020 16.1272%, rgba(32, 32, 32, 0) 60%),
    url("../../../public/images/debit-card-background.png");
  background-position: top rem(170px) right rem(370px), left, right;
  background-repeat: no-repeat, no-repeat, no-repeat;

  margin: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;

  .debit-card-content {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding-bottom: rem(347px);
    padding-top: rem(260px);
    font-family: $font;
    color: $white;
    .debit-card-heading {
      margin-bottom: rem(50px);
      font-weight: 700;
      font-size: 64px;
      line-height: 134%;
    }
    .debit-card-paragraph {
      margin-bottom: rem(100px);
      font-weight: 500;
      font-size: 22px;
      line-height: 200%;
      max-width: rem(680px);
    }
    .debit-link {
      height: fit-content;
      display: flex;
      align-items: center;
      transition: 0.7s;
      .link-text {
        margin: 0;
        padding-right: rem(12px);
        transition: 0.4s;
      }
    }
    .debit-link:hover {
      .link-text {
        padding-right: 30px;
        transition: 0.6s;
      }
      cursor: pointer;
      opacity: 0.5;
      transition: 0.4s;
    }
  }
}

@media (max-width: 1200px) {
  .debit-card {
    background-image: url("../../../public/images/card.png"),
      linear-gradient(178.07deg, #202020 44%, rgba(32, 32, 32, 0) 77.5%),
      url("../../../public/images/debit-card-background-big.png");

    background-size: auto, auto, 1200px auto;
    background-position: top 580px center, top, bottom -600px center;

    .debit-card-content {
      align-items: center;
      padding-top: 100px;
      padding-bottom: 692px;
      text-align: center;
      .debit-card-heading {
        font-size: 48px;
      }
      .debit-card-paragraph {
        font-size: 16px;
      }
    }
  }
}

@media (max-width: 1024px) {
  .debit-card {
    background-size: auto, auto, 1024px auto;
    background-position: top 580px center, top, bottom -520px center;
  }
}

@media (max-width: 768px) {
  .debit-card {
    .debit-card-content {
      .debit-card-heading {
        font-size: 32px;
      }
      .debit-card-paragraph {
        font-size: 14px;
      }
    }
  }
}

@media (max-width: 480px) {
  .debit-card {
    background-size: 90%, auto, 1024px auto;
    background-position: bottom 320px center, top, bottom -520px center;
  }
}
